import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { ThemeProvider } from 'styled-components';
import GlobalStyle from 'theme/GlobalStyle';
import PageContext from 'context';
import { theme } from 'theme/mainTheme';

class MainTemplate extends Component {
  state = { pageType: 'notes' };

  componentDidMount() {
    this.setCurrentPage();
  }

  componentDidUpdate(prevProps, prevState) {
    this.setCurrentPage(prevState);
  }

  setCurrentPage = (prevState = '') => {
    const pageTypes = ['notes', 'twitters', 'articles'];
    const {
      location: { pathname },
    } = this.props;
    const [currentPage] = pageTypes.filter(page => pathname.includes(page));

    if (prevState.pageType !== currentPage) this.setState({ pageType: currentPage });

    // moja wersja, tez dziala
    // this.setState(prevState => {
    //   if (prevState.pageType !== currentPage) return { pageType: currentPage };
    // });
  };

  render() {
    const { children } = this.props;
    const { pageType } = this.state;
    return (
      <div>
        <PageContext.Provider value={pageType}>
          <GlobalStyle />
          <ThemeProvider theme={theme}>{children}</ThemeProvider>
        </PageContext.Provider>
      </div>
    );
  }
}

MainTemplate.propTypes = {
  children: PropTypes.element.isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
  }).isRequired,
};

// withRouter dziala podobnie jak connect z reduxa.
// oplata komponent, jest to higher order komponent. MainTemplate dostanie dodatkowe propsy
export default withRouter(MainTemplate);
